import React, { useEffect } from 'react'
export default function MatrixRain(){

    const rain =()=>{
        let canvas = document.getElementById('matrix')
        let ctx = canvas.getContext('2d')
        let height = canvas.height=window.innerHeight
        let width = canvas.width= window.innerWidth
        let str ="1001010101110101010101010010101000101011101111010101010110101010101010101110000101"
        let matrix = str.split("")
        let font =12
        let col = width/font
        let arr=[]

        for (let i =0;i<col;i++){
            arr[i]=1
        }
        const draw = () => {
            
            ctx.fillStyle="rgba(0,0,0,0.05)"
            ctx.fillRect(0,0,width,height)
            ctx.fillStyle="#086F08"
            ctx.font=`${font}px system-ui`
            for (let i=0;i<arr.length;i++){
                let txt = matrix[Math.floor(Math.random() * matrix.length)]
                ctx.fillText(txt,i*font,arr[i]*font)

                if(arr[i]*font>height&&Math.random()>0.98){
                    arr[i]=0
                }
                arr[i]++
            }
        }
        setInterval(draw, 50)
        window.addEventListener('resize', () => {
            height = canvas.height = window.innerHeight
            width = canvas.width = window.innerWidth
            draw()
        })

    }

    useEffect(()=>{
        rain()
    },[])
    return(
        <canvas id="matrix" style={{position:'fixed',top:0,left:0,opacity:1,zIndex:-1,height:'100%',width:'100%'}}>

        </canvas>
    )
}